import React from 'react'
import { Link, NavLink, withRouter } from 'react-router-dom'
import UpdatareInformatii from './administrare/UpdatareInformatii'

const NavBar = (props) => {
  return (
    <nav className="navbar navbar-default">
      <div className="container-fluid">
        <div className="navbar-header">
          <Link to="/" className="navbar-brand">Unicarm Printer Monitor</Link>
        </div>
        <ul className="nav navbar-nav navbar-center">

          <li className="dropdown">
            <NavLink className="dropdown-toggle" data-toggle="dropdown" to="/"><span>Administrare</span>&nbsp;<span className="caret"></span></NavLink>
            <ul className="dropdown-menu">
              <li className="dropdown-header">Updatare</li>
              <li><Link to="/UpdatareInformatii">Updatare Informatii</Link></li>
              <li><Link to="/Administrare">Updatare Status Imprimanta</Link></li>
              <li role="separator" className="divider"></li>
              <li className="dropdown-header">Introducere Informatii</li>
              <li><Link to="/Administrare">Introducere Imprimanta Noua</Link></li>
              <li><Link to="/Administrare">Introducere Magazin Nou</Link></li>
              <li><Link to="/Administrare">Introducere User Nou</Link></li>
              <li><Link to="/Administrare">Introducere Reparatie</Link></li>
              <li role="separator" className="divider"></li>
              <li className="dropdown-header">Vizualizare Date</li>
              <li><Link to="/Administrare">Vizualizare Listare Magazine</Link></li>
              <li><Link to="/Administrare">Vizualizare Listare Useri</Link></li>
              <li><Link to="/Administrare">Vizualizare Listare Imprimante</Link></li>
            </ul>
          </li>

          <li className="dropdown">
            <NavLink className="dropdown-toggle" data-toggle="dropdown" to="/"><span>Probleme Imprimante</span>&nbsp;<span className="caret"></span></NavLink>
            <ul className="dropdown-menu bold">
              <li className="dropdown-header">Sesizeaza o problema</li>
              <li><Link to="/">Sesizare Problema Noua</Link></li>
              <li><Link to="/">Anulare Sesizare</Link></li>
            </ul>
          </li>

          <li className="dropdown">
            <NavLink className="dropdown-toggle" data-toggle="dropdown" to="/"><span>Rapoarte</span>&nbsp;<span className="caret"></span></NavLink>
            <ul className="dropdown-menu bold">
              <li className="dropdown-header">Vizializare Rapoarte</li>
              <li><Link to="/">Pret reparatii per imprimante</Link></li>
              <li><Link to="/">Unde a fost imprimanta pana acuma</Link></li>
              <li><Link to="/">Un magazin ce imprimante a avut pana acuma</Link></li>
              <li><Link to="/">Un magazin ce probleme a avut la imprimante</Link></li>
              <li><Link to="/">Pret reparatii per magazin</Link></li>
              <li><Link to="/">Istorie reparatii imprimante </Link></li>
              <li><Link to="/">Ce probleme a avut imprimanta pana acuma</Link></li>
            </ul>
          </li>

        </ul>

        <ul className="nav navbar-nav navbar-right">
          <li><Link to="/Login"><span className="glyphicon glyphicon-user"></span> &nbsp;Log In</Link></li>
          <li><Link to="/"><span className="glyphicon glyphicon-log-in"></span> &nbsp;Log Out</Link></li>
        </ul>
      </div>
    </nav>

  )
}

export default withRouter(NavBar)
