import React, { Component } from 'react'
import './component_style/Login.css'
// import { connect } from 'react-redux'

class Login extends Component {
    state = {
        username: "",
        password: "",
        auth: ""
    }


    handleSubmit = (e) => {
        e.preventDefault();
        document.getElementById("username").value = "";
        document.getElementById("password").value = "";
        // this.props.checkAuth(this.state);
    }


    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleClose = (e) => {
        this.props.history.push('/');
    }

    render() {
        return (
            <div className="popup">
                <div className="wrapper-login">
                    <div className="x" onClick={this.handleClose}>X</div>
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                            <input type="text" className="form-control" id="username" placeholder="User" onChange={this.handleChange} />
                            <label className="labelControl" htmlFor="username" >User</label>
                        </div>
                        <div className="form-group">
                            <input type="password" className="form-control" id="password" placeholder="Parola" onChange={this.handleChange} />
                            <label className="labelControl" htmlFor="password" >Parola</label>
                        </div>
                        <div className="form-group">
                            <div className="center">
                                <button type="submit" className="btn btn-default" onClick={this.handleSubmit}>Login</button>

                            </div>
                        </div>
                    </form>
                </div>
            </div >
        )
    }
}
export default Login;
